import $ from '$';
import App from '../app/index.js';

/**
 * 动态图表启动器
 * 职责：1、负责创建一个vue实例，用于渲染统计图表的全部内容
 *      2、根据PC端提供的动态皮肤参数，装载皮肤包入口文件
 * 
 * @export
 * @class DynamicChartLauncher
 */
export default class DynamicChartLauncher {
  constructor() {
    this.service = null;
    this.container = null;
    this.statObj = null;
    this.app = null;
    this.currentSkin = null;
    this.parmas = {
      chartInfo: null //当前皮肤信息 PC端提供
    };
    this.config = {
      workspace: '__DynamicDiagram'
    };
  }

  init(statisticService) {
    this.service = statisticService;
  }

  /**
   * 启动皮肤
   * 
   * @param {any} chartContainer
   * @param {any} convertedData
   * @param {any} currentChartInfo 当前皮肤信息 PC端提供
   * 
   * @memberOf DynamicChartLauncher
   */
  launch(chartContainer, statObj, currentChartInfo) {
    let service = this.service;
    let lang = service.parent.langProperties;
    this.container = chartContainer, this.statObj = statObj;
    this.parmas.chartInfo = currentChartInfo;


    let _self = this;
    this.app = new App(chartContainer, this.statObj.convertedData, lang); //创建vue实例
    this.app.registerEvent("onSeeDetail", function(userId) { //处理点击查看详情事件
      var user_response,
        answers = _self.statObj.answers,
        a;
      for (var i = 0, len = answers.length; i < len; i++) {
        a = answers[i];
        if ($.inArray(userId, a.userIds) >= 0) {
          var ans = JSON.parse(a.answer);
          user_response = $.extend(true, [], ans["user_response"]);
          break;
        }
      }
      if (!!user_response) {
        var eventData = {
          studentAnswer: user_response,
          userId: userId
        };

        service.dispatchEvent("Event_SeeStudentAnswer", PresenterEventType.IC_PLAYER_EVENT, eventData);
      }
      var eventData = {
        service: "layout",
        method: "seeStudentAnswer"
      };
      service.dispatchEvent("statistics_call", PresenterEventType.IC_PLAYER_EVENT, eventData);
    });

    $.getJSON(currentChartInfo.baseUrl + "/config.json", (config) => { //读取对应皮肤包的配置文件
      this.loadSkin(this.app.$el, this.statObj.convertedData, currentChartInfo, config, lang).done(() => { //皮肤加载完后，绑定事件
        this.bindEvent();
      });
    });
  }

  bindEvent() {
    if (this.service && this.service.parent && this.currentSkin['instance']) {
      this.service.parent.$addEventListener("DynamicChartLauncher", PresenterEventType.IC_PLAYER_EVENT, (eventData) => {
        this.currentSkin['instance']['startAnimation'].call(this.currentSkin['instance']); //
      });
    }
  }

  loadSkin(chartContainer, convertedData, currentChartInfo, config, lang) {
    let defer = $.Deferred();
    let baseUrl = currentChartInfo.baseUrl;
    let name = btoa(currentChartInfo.baseUrl); //把baseUrl转码
    let oHead = document.getElementsByTagName('HEAD').item(0);

    //1、去掉已经加载的script和css
    let scripts = document.getElementsByTagName("script");
    let s,
      target;
    for (let i = 0, len = scripts.length; i < len; i++) {
      s = scripts[i];
      if (s.getAttribute('skin') === name) {
        target = s;
        break;
      }
    }
    if (target) {
      oHead.removeChild(target);
    }

    let links = document.getElementsByTagName("link");
    let l,
      link;
    for (let i = 0, len = links.length; i < len; i++) {
      l = links[i];
      if (l.getAttribute('skin') === name) {
        link = l;
        break;
      }
    }
    if (link) {
      oHead.removeChild(link);
    }

    let scriptSrc = baseUrl + "/index.js";
    let oScript = document.createElement("script");
    oScript.type = "text/javascript";
    oScript.src = scriptSrc;
    oScript.setAttribute("skin", name);
    oHead.appendChild(oScript);

    let installOpt = {
      'chartContainer': chartContainer,
      'data': convertedData,
      'chartInfo': currentChartInfo,
      'config': config,
      'lang': lang
    };

    oScript.onload = () => {
      if (!window[this.config.workspace] || !(window[this.config.workspace] instanceof Array))
        return;
      let skinCaches = window[this.config.workspace];
      this.targetIndex = skinCaches.length - 1;
      let target = skinCaches[this.targetIndex]; //拿到最新加入的皮肤

      this.currentSkin = target;

      target && target['instance'] && target['instance'].install && target['instance'].install(installOpt);
      defer.resolve();
    }

    return defer.promise();
  }

  destory() {
    this.app && this.app.$destory(); //销毁实例
    this.container.innerHTML = ''; //删除容器下的所有dom元素
  }
}


